/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --font-body: "Lato", sans-serif;
  --font-display: "League Spartan", sans-serif;
}

:root {
  --nwcGreen: hsl(167, 100%, 20.2%);
  --nwcLightGreen: hsl(120, 55.5%, 44.9%);
  --nwcRed: hsl(355, 84.5%, 54.5%);
  --nwcYellow: hsl(47, 100%, 46.1%);
  --nwcBlue: hsl(220, 100%, 23.1%);
  --nwcLightBlue: hsl(207, 100%, 40.6%);
  --green: hsl(166, 100%, 18.4%);
  --lightgreen: hsl(247, 9.8%, 36.1%);
  --fadeRed: hsl(360, 82.5%, 57.5%);
  --brightRed: hsl(358, 72.6%, 45.9%);
  --red: hsl(358, 84.6%, 51.8%);
  --darkBlue: hsl(235, 52.2%, 9%);
  --gold: hsl(54, 69.2%, 84.7%);
  --lightGold: hsl(54, 71.4%, 97.3%);
  --black: hsl(0, 0%, 0%);
  --richBlack: hsl(0, 0%, 6%);
  --lightGrey: hsl(210, 1%, 60.8%);
  --grey: hsl(0, 0%, 50.2%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

.container {
  width: min(90%, 75em);
  margin-inline: auto;
}
.panels {
  height: 1.25rem;
  background-color: var(--nwcLightGreen);
  width: 100%;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-family: var(--font-display);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.btn__contact a, .btn__login a {
  font-family: var(--font-display);
  font-size: 1.125rem;
  font-weight: 700;
  padding: 0.625rem 1.25rem;
  color: var(--nwcGreen);
  background-color: var(--white);
  border: 0.0625rem solid var(--white);
  border-radius: 0.3125rem;
  margin-left: 1.25rem;
  cursor: pointer;
}
@media (max-width: 56.24875em) {
  .btn__contact a, .btn__login a {
    margin-left: 0;
  }
}
.btn__contact a:hover, .btn__login a:hover {
  background-color: var(--nwcLightGreen);
  border: 0.0625rem solid var(--nwcLightGreen);
  color: var(--white);
  transition: color 250ms ease-in-out;
}

.header {
  background-color: var(--nwcGreen);
  display: flex;
  flex-direction: row;
  align-items: center;
}
.header__logo img {
  margin: 1.25rem 0 1.5625rem 1.25rem;
}
.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.header__media {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0.625rem;
}
.header__media input[type=text] {
  font-size: 1rem;
  color: var(--lightGrey);
}
.header__media--placeholder {
  padding: 0.3125rem;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--newGreen);
}
.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.header__menu ul {
  display: flex;
  flex-direction: row;
}
.header__menu li {
  font-family: var(--font-display);
  font-size: 1.125rem;
  font-weight: 700;
  list-style: none;
  margin-left: 1.5625rem;
}
.header__menu li a {
  color: var(--white);
  cursor: pointer;
}
.header__menu li a:hover,
.header__menu li a:visited,
.header__menu li a:active {
  color: var(--gold);
}

.nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.card {
  padding: 1.25rem;
}
.card__button {
  width: 1rem;
  height: 1rem;
  border-radius: 0.9375rem;
  background-color: var(--nwcLightGreen);
}